<template>
  <div class="playlist" v-if="playlistMsg != undefined">
    <Loading />
    <div class="header">
      <div
        class="mask"
        :style="'background-image:url(' + playlistMsg.coverImgUrl + ')'"
      ></div>
      <div class="title">
        <div class="img">
          <span class="lsthd_icon">歌单</span>
          <img :src="playlistMsg.coverImgUrl" alt="" />
          <span class="play-count">
            {{ playlistMsg.playCount | formatPlayCount }}</span
          >
        </div>
        <div class="con">
          <h3>{{ playlistMsg.name }}</h3>
          <p>
            <template v-if="playlistMsg.creator">
              <img
                :src="
                  playlistMsg.creator.avatarUrl +
                    '?imageView=1&type=webp&thumbnail=252x0'
                "
                alt=""
                class="photo"
              />
              <span class="nickname">{{ playlistMsg.creator.nickname }}</span>
            </template>
          </p>
        </div>
      </div>
    </div>
    <section v-if="!$root.isloading">
      <h5>歌曲列表</h5>
      <SongItem
        v-for="(song, index) in newSongs"
        :key="song.id"
        :item="song"
        :index="index"
        @update:music="$emit('update:music', $event)"
        @update:playlist="$emit('update:playlist', newSongs)"
        :isHot="isHot"
        :isShowHot="isShowHot"
        :isplaylist="isplaylist"
      ></SongItem>
      <h5>精彩评论</h5>
      <Comments :commentsList="commentsList"></Comments>
    </section>
  </div>
</template>

<script>
import SongItem from '@/components/SongItem';
import Loading from '@/components/Loading';
import Comments from '@/components/Comments';
export default {
  name: 'PlayList',
  components: {
    SongItem,
    Loading,
    Comments
  },
  data() {
    return {
      playlistMsg: [],
      newSongs: [],
      isHot: false,
      isShowHot: true,
      isplaylist: true,
      count: 0,
      id: null,
      commentsList: []
    };
  },
  filters: {
    formatPlayCount: function(value) {
      let k = 10000;
      let sizes = ['', '万', '亿', '万亿'];
      if (value < k) {
        return value;
      } else {
        let i = Math.floor(Math.log(value) / Math.log(k));
        value = (value / Math.pow(k, i)).toFixed(1);
        return value + sizes[i];
      }
    }
  },
  computed: {
    setDescription: function() {
      // console.log(typeof this.playlistMsg);
      return this.playlistMsg.description
        ? this.playlistMsg.description.toString().split('\n')
        : '';
    }
  },
  created() {
    this.id = this.$route.query.id;
  },
  watch: {
    id(n) {
      console.log(n);
      this.$root.isloading = true;
      // https://music.163.com/api/playlist/detail?id=3110865674
      this.axios
        .get('https://music.kele8.cn/playlist/detail?id=' + n)
        .then(response => {
          console.log(response.data.playlist);
          this.playlistMsg = response.data.playlist;
          let songlist = response.data.playlist.trackIds
            .map(item => {
              return item.id;
            })
            .slice(0, 20)
            .join(',');

          this.axios
            .get('https://music.kele8.cn/song/detail', {
              params: {
                ids: songlist
              }
            })
            .then(response => {
              this.newSongs = response.data.songs;
            })
            .finally(() => {
              this.count = this.count + 1;
            });
        })
        .finally(() => {
          this.count = this.count + 1;
        });
      this.axios
        .get('https://music.kele8.cn/comment/playlist', {
          params: {
            id: n
          }
        })
        .then(response => {
          // console.log(response.data.comments);
          this.commentsList = response.data.comments;
        });
    },
    count(n) {
      if (n >= 2) {
        this.count = 0;
        this.$root.isloading = false;
      }
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.id = to.query.id;
    next();
  },
  activated() {
    this.id = this.$route.query.id;
  }
};
</script>

<style lang="less" scoped>
.playlist {
  width: 100%;
  height: auto;
  .header {
    position: relative;
    color: #fff;
    width: 100%;
    height: 180px;
    padding: 0 10px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    overflow: hidden;
    .mask {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50%;
      transform: scale(1.5);
      filter: blur(25px) brightness(0.6);
      z-index: -1;
    }
    .title {
      display: flex;
      .img {
        position: relative;
        .lsthd_icon {
          position: absolute;
          z-index: 3;
          top: 10px;
          left: 0;
          padding: 0 8px;
          height: 17px;
          color: #fff;
          font-size: 9px;
          text-align: center;
          line-height: 17px;
          background-color: rgba(217, 48, 48, 0.8);
          border-top-right-radius: 17px;
          border-bottom-right-radius: 17px;
        }
        .play-count {
          font-size: 12px;
          padding: 3px 3px 0 0;
          position: absolute;
          top: 0;
          right: 0;
          color: white;
          text-shadow: 0 0 1px black;
          &::before {
            content: '';
            background: no-repeat
              url('data:image/svg+xml;base64,');
            background-position: left center;
            background-size: auto 90%;
            width: 0.8em;
            height: 0.9em;
            display: inline-block;
            // vertical-align: middle;
          }
        }
        img {
          display: inline-block;
          max-width: 126px;
          max-height: 126px;
          min-width: 126px;
          min-height: 126px;
        }
      }
      .con {
        margin-left: 16px;
        p {
          margin-top: 20px;
          height: 30px;
          display: flex;
          font-size: 14px;
          align-items: center;
          img {
            position: relative;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            &::before {
              content: '';
              position: absolute;
              right: -5px;
              bottom: 0;
              width: 12px;
              height: 12px;
              background-image: url(//s3.music.126.net/mobile-new/img/usericn_2x.png?6423c0681bfb3358d5c684c9b998e412=);
              background-repeat: no-repeat;
              background-size: 75px auto;
            }
          }
          .nickname {
            padding-left: 5px;
          }
        }
      }
    }
    h3 {
      font-size: 17px;
    }
  }
  section {
    width: 100%;
    h5 {
      font-size: 12px;
      padding: 5px 0 5px 10px;
      background: #eeeff0;
    }
  }
}
</style>
